<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <p>
      在下面填写你需要加解密的内容即可  <el-tag type="warning">默认加密输出内容每次都不一致</el-tag>

    </p>

    <el-form :inline="true" :model="formInline" class="demo-form-inline">
      <el-form-item label="公钥或私钥"
                    prop="key"
                    :rules="[
                       {required:true,message:'公钥或私钥 不能为空'}
                    ]"
      >
        <el-input
            type="textarea"
            style="width: 800px"
            :autosize="{ minRows: 4 }"
            v-model="formInline.key" placeholder="一般而言，公钥用来加密，私钥用来解密  在这里填写公钥，则是用公钥进行加密"></el-input>
      </el-form-item>
      <br>
      <el-form-item label="待加解内容"
                    prop="msg"
                    :rules="[
                       {required:true,message:'待加密内容 不能为空'}
                    ]"
      >
        <el-input
            type="textarea"
            style="width: 800px"
            :autosize="{ minRows: 4 }"
            v-model="formInline.msg" placeholder="待加密内容"></el-input>
      </el-form-item>
      <br>
      <el-form-item label="待输出内容">
        <el-input
            type="textarea"
            style="width: 800px"
            :autosize="{ minRows: 4 }"
            v-model="formInline.output" placeholder="这里将展示被加密或解密的内容"></el-input>
      </el-form-item>
      <br>
      <el-form-item>
        <el-button type="primary" @click="encrypt">公钥加密</el-button>
        <el-button type="primary" @click="decrypt">私钥加密</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'Encrypt',
  props: {
    msg: String
  },
  data() {
    return {
      formInline: {
        key: '',
        msg: '',
        output: ''
      }
    }
  },
  methods: {
    decrypt() {
      const key=this.formInline.key
      const msg=this.formInline.msg
      const output=this.formInline.output
      console.log('decrypt!');
      axios({
        method:'post',
        url:'/decrypt',
        data:{
          key,msg
        }
      }).then(res=>{
        console.log(res.data)
        if(res.data.code===200){
          this.formInline.output=res.data.data

        }else{
          this.formInline.output=res.data.msg

        }
      })
    },

    encrypt() {
      const key=this.formInline.key
      const msg=this.formInline.msg
      const output=this.formInline.output
      console.log('encrypt!');
      axios({
        method:'post',
        url:'/encrypt',
        data:{
          key,msg

        }
      }).then(res=>{
        console.log(res.data)
        if(res.data.code===200){
          this.formInline.output=res.data.data

        }else{
          this.formInline.output=res.data.msg

        }

      })
    }
  }
}
</script>

